.history_details {
    background-color: #fff;
    height: 100%;
    overflow-x: hidden;
    padding-top: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: 0.5s;
    width: 0;
    z-index: 1;
    z-index: 999999;
}
.history_details h3 {
    padding-left: 14px;
}
.history_details a {
    color: #818181;
    display: block;
    font-size: 25px;
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    transition: 0.3s;
}

.history_details a:hover {
    color: #f1f1f1;
}

.history_details .closebtn {
    font-size: 36px;
    margin-left: 50px;
    position: absolute;
    right: 25px;
    top: 6px;
}
.align-middle {
    vertical-align: middle !important;
}
